import { auth, facebookAuthProvider, googleAuthProvider } from "../../../common/api/firebaseConnection";
import { createCommand } from "../../../common/utilities/createCommand";
import { authenticateFirebaseWithProviderEffect, writeUserToStateEffect } from "../../../common/commands/authenticateFirebaseWithProviderEffect";
import { createNewTeacherEffect } from "./createNewTeacherEffect";
import { User } from "firebase/auth";
import { FirebaseUserInfo } from "../../../common/types/utility";
import { select } from "redux-saga/effects";
import { selectUserProfile } from "../../../common/selectors/commonSelectors";

export const alreadyAuthenticatedUser = createCommand<User>(
  'alreadyAuthenticatedUser',
  function*({payload: user}) {
    yield writeUserToStateEffect(user);
  }
)

export const authenticateWithFirebase = createCommand<void>(
  'authenticateWithFirebase',
  function* () {
    yield authenticateFirebaseWithProviderEffect(auth, googleAuthProvider);
    const userProfile: FirebaseUserInfo = yield select(selectUserProfile);
    yield createNewTeacherEffect(userProfile);
  },
);

export const authenticateFacebook = createCommand<void>(
  'authenticateWithFacebook',
  function*() {
    yield authenticateFirebaseWithProviderEffect(auth, facebookAuthProvider);
    const userProfile: FirebaseUserInfo = yield select(selectUserProfile);
    yield createNewTeacherEffect(userProfile);
  }
)

